import {
  Scene,
  MeshPhongMaterial,
  SphereBufferGeometry,
  PerspectiveCamera,
  WebGLRenderer,
  Mesh,
  DirectionalLight,
  Object3D,
  BufferGeometry,
  Float32BufferAttribute,
  Points,
  PointsMaterial,
  MathUtils,
} from "three";
let scene = new Scene();
let camera = new PerspectiveCamera(40, window.innerWidth / window.innerHeight, 1, 10000);
camera.position.z = 1200;
camera.position.x = 1200;
camera.position.y = 1200;


let camW = new Object3D();

camW.add(camera);

scene.add(camW);

let render = new WebGLRenderer();
render.setSize(window.innerWidth, window.innerHeight);
render.setPixelRatio(devicePixelRatio);

document.body.appendChild(render.domElement);

let light = new DirectionalLight(0xd3ba4a, 1, 100);
light.position.set(5, 5, -5);

scene.add(light);

let light1 = new DirectionalLight(0xffffff);
light1.position.set(-5, -5, 5);

scene.add(light1);

let sphereGeo = new SphereBufferGeometry(350, 32, 32);
let sphereMaterial = new MeshPhongMaterial({
  shininess: 40,
});

let sphere = new Mesh(sphereGeo, sphereMaterial);


let sphere1 = new Mesh(
  new SphereBufferGeometry(550, 16, 8),
  new MeshPhongMaterial({
    color: 0xffffff,
    wireframe: true,
  })
);


scene.add(sphere1);
scene.add(sphere);

var geometry = new BufferGeometry();
var vertices = [];

for (var i = 0; i < 10000; i++) {
  vertices.push(MathUtils.randFloatSpread(2000)); // x
  vertices.push(MathUtils.randFloatSpread(2000)); // y
  vertices.push(MathUtils.randFloatSpread(2000)); // z
}

geometry.setAttribute(
  "position",
  new Float32BufferAttribute(vertices, 3)
);

console.log(sphere.position)

var particles = new Points(
  geometry,
  new PointsMaterial({ color: 0xd3ba4a })
);
scene.add(particles);
let r
function animate(time) {
  requestAnimationFrame(animate);
  render.render(scene, camera);
  r = time * 0.001

  camera.fov = 40 + 5 * Math.sin( 0.1 * r * Math.PI)
  camera.updateProjectionMatrix()

  camW.rotation.z = r * .5;
  camW.rotation.x = r * .3;
  sphere1.rotation.y = r 

  camera.lookAt(sphere.position);
}
animate();
